			<div class="link-card-footer mt-auto py-3 d-none d-md-flex justify-content-end">
				<div class="link-card-btn rounded-circle d-flex justify-content-center align-items-center mx-2" v-b-tooltip.hover title="Create personalized invite" v-if="!isOneOnMany">
					<b-icon icon="person-plus" class="text-muted" font-scale="1.3"></b-icon>
				</div>
				<div class="link-card-btn rounded-circle d-flex justify-content-center align-items-center mx-2" v-b-tooltip.hover title="Copy Link">
