				<div class="link-card-btn rounded-circle d-flex justify-content-center align-items-center mx-2" v-b-tooltip.hover title="Edit">
					<b-icon icon="pencil" class="text-muted" font-scale="1.3"></b-icon>
				</div>
				<b-dropdown variant="link" toggle-class="text-decoration-none p-0 box-shadow-none" right no-caret menu-class="p-0 shadow border-0">
					<template #button-content>
						<div class="link-card-btn ml-2 rounded-circle d-flex justify-content-center align-items-center" v-b-tooltip.hover title="More actions">
