	</b-card>
</template>

<script>
	import LinkImageTooltip from "@/components/LinkImageTooltip";

	export default {
		components: { LinkImageTooltip },

		props: { invite: { type: Object, required: true } },

		computed: {
			isOneOnMany() {
				return this.invite.type == "oneOnMany";
			},

			durationTxt() {
				let duration = this.invite.duration;

				if (duration <= 30) return "30 min";

				let hours = duration / 60;

				let minutes = duration % 60;

				if (minutes == 0) {
					return `${hours} hr`;
				} else {
					return `${hours.toString().split(".")[0]} hr ${minutes} min`;
				}
			},

			location() {
				let location = this.invite.location?.name;

				if (!location) return "";

				if (location == "google") return "Google Meet";

				return location;
			},
		},
	};
</script>
